import React, { Component } from 'react';
import './App.css';
import { FormControl, Button } from 'react-bootstrap';
import LoadingScreen from './LoadingScreen';
import Protected from './protected.png';
import Unprotected from './unprotected.png';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      url: '',
      isLoading: false,
      verified: true
    }
  }

  valueSetter(ev) { this.setState({ [ev.target.name] : ev.target.value }) }
  
  // Replace Right Keyword with Right IP Address
  goToRightURL() {
    let requestURI = '/';
    let slashCounter = '';

    for(let i = 0; i < this.state.url.length; i++) {
      if(slashCounter > 2) {
        requestURI += this.state.url.toString().substr(i, 1);
      } else {
        if(this.state.url.toString().substr(i, 1) === '/') {
          slashCounter++;
        }
      }
    }

    document.getElementById('myIframe').src = 'http://209.97.164.35' + requestURI;
  }
 
  // Ganti URL Iframe
  urlChangeHandler() {
    this.setState({ isLoading: true });
    const daftarURL = ['jointogel', 'jointgl', 'jintogel']
    let joinTogel = false;

    for(let i = 0; i < daftarURL.length; i++) {
      if(this.state.url.toString().toLowerCase().includes(daftarURL[i])) {
        joinTogel = true;
      }
    }

    const hasHTTPBefore = this.state.url.toString().substr(0, 7).toLowerCase() !== 'http://' && this.state.url.toString().substr(0, 8).toLowerCase() !== 'https://';

    if(joinTogel) {
      alert('Koneksi terlindungi');
      this.setState({ verified: true });

      if(hasHTTPBefore) { this.setState({ url: 'http://' + this.state.url }, ()=> { this.goToRightURL() })}
      else { this.goToRightURL() }
    } else {
      alert('Situs ini belum terdaftar. Menghubungkan menggunakan jaringan normal.');
      this.setState({verified: false});

      if(hasHTTPBefore) { this.setState({url: 'http://' + this.state.url}, ()=> {
        document.getElementById('myIframe').src = this.state.url
      })} else { document.getElementById('myIframe').src = this.state.url }
    }
  }

  // Enter = Go!
  isSubmit(ev) { if(ev.charCode === 13) { this.urlChangeHandler() }}

  // Menentukan Protected atau Tidak
  Classifier() {
    if(this.state.verified) {
      return (
        <div className='classifier pro'>
          <img src={Protected} alt='Protected' className='protected'/>
          <span className='desktopOnly'>JARINGAN TERLINDUNGI</span>
        </div>
      )
    } else {
      return (
        <div className='classifier unp'>
          <img src={Unprotected} alt='Unprotected' className='protected'/>
          <span className='desktopOnly'>JARINGAN TIDAK TERLINDUNGI</span>
        </div>
      )
    }
  }

  render() {
    const IsLoading = ()=> {
      if(this.state.isLoading) { return <LoadingScreen /> }
      else { return null }
    }
    
    return (
      <div className='App'>
        <IsLoading />

        <div className='headerContainer'>
          {this.Classifier()}

          <FormControl type='url' placeholder='Masukkan alamat website di sini' name='url' onChange={this.valueSetter.bind(this)} onKeyPress={this.isSubmit.bind(this)} value={this.state.url} />

          <Button variant='primary' onClick={this.urlChangeHandler.bind(this)}>Go</Button><br />
        </div>

        <iframe id='myIframe' frameBorder='0' height='88%' width='100%' title='Bypass Internet Positif Nawala' onLoad={()=> {this.setState({isLoading: false})}}></iframe>
      </div>
    );
  }
}

export default App;